
import { computed, defineComponent, ref } from 'vue'

export default defineComponent({
  setup() {
    const header = ref<HTMLElement | null>(null)

    const height = computed(() => {
      if (header.value) {
        return `calc(100% - ${header.value.clientHeight})`
      }
      return '100%'
    })

    return {
      height,
      header,
    }
  },
})
