
import { defineComponent } from 'vue'

export default defineComponent({
  props: {
    padding: {
      type: Boolean,
      default: false,
    },
  },
  setup(props) {
    const classes = ['r-page w-full h-full overflow-y-auto']

    if (props.padding) {
      classes.push('p-6')
    }

    return {
      classes,
    }
  },
})
